"use client";

import React, { useState, useEffect } from 'react';
import { MapPin, Loader2, ChevronDown } from 'lucide-react';
import dynamic from 'next/dynamic';
import { Card, CardContent, CardHeader, CardTitle } from '@/app/components/ui/card';
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue 
} from '@/components/ui/select';
import type { WaterQualityData, WaterQualityRecord, LocationCoordinates } from './types';
import type { LatLngTuple } from 'leaflet';

// Dynamically import Leaflet components
const MapContainer = dynamic(
  () => import('react-leaflet').then(mod => mod.MapContainer),
  { ssr: false }
);

const TileLayer = dynamic(
  () => import('react-leaflet').then(mod => mod.TileLayer),
  { ssr: false }
);

const Marker = dynamic(
  () => import('react-leaflet').then(mod => mod.Marker),
  { ssr: false }
);

const waterQualityData: WaterQualityRecord = {
  'New York': { quality: 'Good', details: 'pH: 7.2, Turbidity: Low' },
  'Los Angeles': { quality: 'Fair', details: 'pH: 6.8, Turbidity: Medium' },
  'Chicago': { quality: 'Excellent', details: 'pH: 7.0, Turbidity: Very Low' },
  'Houston': { quality: 'Poor', details: 'pH: 6.5, Turbidity: High' },
};

const locations: LocationCoordinates = {
  'New York': [40.7128, -74.0060],
  'Los Angeles': [34.0522, -118.2437],
  'Chicago': [41.8781, -87.6298],
  'Houston': [29.7604, -95.3698],
};

interface MapMarkerProps {
  position: LatLngTuple;
  onClick: () => void;
}

const MapMarker: React.FC<MapMarkerProps> = ({ position, onClick }) => {
  return (
    <Marker 
      position={position} 
      eventHandlers={{ click: onClick }}
    />
  );
};

interface MapComponentProps {
  onLocationSelect: (location: string) => void;
}

const MapComponent: React.FC<MapComponentProps> = ({ onLocationSelect }) => {
  const [selectedLocation, setSelectedLocation] = useState<string | null>(null);

  const handleMarkerClick = (location: string) => {
    setSelectedLocation(location);
    onLocationSelect(location);
  };

  return (
    <div className="h-[400px] w-full rounded-lg overflow-hidden">
      <MapContainer
        center={[39.8283, -98.5795]}
        zoom={4}
        className="h-full w-full"
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        {Object.entries(locations).map(([name, coords]) => (
          <MapMarker
            key={name}
            position={coords}
            onClick={() => handleMarkerClick(name)}
          />
        ))}
      </MapContainer>
    </div>
  );
};

export default function WaterQualityMap() {
  const [selectedLocation, setSelectedLocation] = useState<string | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [result, setResult] = useState<WaterQualityData | null>(null);

  const handleLocationSelect = async (location: string) => {
    setLoading(true);
    setSelectedLocation(location);
    
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000));
    setResult(waterQualityData[location]);
    setLoading(false);
  };

  const getQualityColor = (quality: string): string => {
    switch (quality) {
      case 'Excellent':
        return 'text-green-600';
      case 'Good':
        return 'text-blue-600';
      case 'Fair':
        return 'text-yellow-600';
      case 'Poor':
        return 'text-red-600';
      default:
        return 'text-gray-600';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <Card className="max-w-4xl mx-auto">
        <CardHeader>
          <CardTitle className="text-center text-2xl font-bold">
            Water Quality Map Analysis
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <Select onValueChange={handleLocationSelect}>
                <SelectTrigger>
                  <SelectValue placeholder="Select a location" />
                </SelectTrigger>
                <SelectContent>
                  {Object.keys(locations).map((location) => (
                    <SelectItem key={location} value={location}>
                      {location}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              
              <MapComponent onLocationSelect={handleLocationSelect} />
              
              <p className="text-sm text-gray-500 text-center">
                Click on a marker or select a location from the dropdown
              </p>
            </div>

            <div className="bg-white rounded-lg p-6 border h-fit">
              {loading ? (
                <div className="flex items-center justify-center h-full">
                  <Loader2 className="w-8 h-8 animate-spin text-blue-500" />
                </div>
              ) : selectedLocation && result ? (
                <div className="space-y-4">
                  <div className="flex items-center gap-2">
                    <MapPin className="w-5 h-5 text-blue-500" />
                    <h3 className="text-lg font-semibold">{selectedLocation}</h3>
                  </div>
                  
                  <div className="space-y-2">
                    <p className="text-gray-600">Water Quality:</p>
                    <p className={`text-2xl font-bold ${getQualityColor(result.quality)}`}>
                      {result.quality}
                    </p>
                  </div>
                  
                  <div className="pt-4 border-t">
                    <h4 className="font-medium mb-2">Detailed Analysis:</h4>
                    <p className="text-gray-600">{result.details}</p>
                  </div>
                </div>
              ) : (
                <div className="text-center text-gray-500">
                  <MapPin className="w-12 h-12 mx-auto mb-3 opacity-50" />
                  <p>Select a location to view water quality data</p>
                </div>
              )}
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}